import React from 'react'
import './content.css'
import FadeIn from 'react-fade-in';
import ScrollAnimation from 'react-animate-on-scroll';

const Content = () => {
    return (
        <>
            <div className="content-pr">
                <ScrollAnimation animateIn="animate__fadeInRight">
                    <div className="content" id="content">
                        <div>
                            <img src={require('../../assets/imgs/me.jpg')} className="foto" />
                        </div>
                        <div>
                            <p id="top-ap">
                                <p className="h2">Sobre mim...</p>
                                <ul id="text-ap">
                                    Olá, me chamo Caíque Santos Goulart, tenho dezenove anos, sou estudante de ciência da
                                    computação, atualmente no sexto semestre. Me interesso por tecnologia em geral desde a
                                    minha infância. No presente momento trabalho como estagiário em service desk, porém busco
                                    por uma oportunidade em desenvolvimento.
                        </ul>
                            </p>
                        </div>
                    </div>
                    </ScrollAnimation>
                </div>
        </>
    )
}

export default Content