import React from 'react'
import ScrollAnimation from 'react-animate-on-scroll';
import { Button } from 'semantic-ui-react'
import { Image, Reveal } from 'semantic-ui-react'
import { Item } from 'semantic-ui-react'
import './atvextra.css'

const Atvextra = () => {
    return (
        <div className="content-atvextra">
            <div className="atvextra">
                <p>
                    <p className="h2atv">Projetos e extra-curriculares...</p>
                    <ScrollAnimation animateIn='animate__fadeInRight'>
                        <Item.Group>
                            <Item>
                                <Item.Image className="img" size='medium' src={require('../../assets/imgs/port2.png')} />
                                <Item.Content>
                                    <p className="header-1">Primeiro Portfólio </p>
                                    <Item.Description>
                                        <p className="cont-">
                                            Este foi o primeiro portfólio criado por mim, utilizando apenas HTML5 e CSS3. E
                                            quando comparado com este atual a diferença é perceptível.
                                        </p>
                                        <a className="link" target="blank" href="https://caiquesgoulart.github.io">
                                        <Button className="bt" inverted color='yellow'>
                                          Confira este projeto
                                        </Button>
                                        </a>
                                    </Item.Description>
                                </Item.Content>
                            </Item>
                            <Item>
                                <Item.Content>
                                    <p className="header-r">WECTI - Unicid</p>
                                    <Item.Description>
                                        <p className="cont-">
                                            Participação da 4ª edição do workshop educacional continuado a T.I (WECTI),
                                            realizado pela Unicid cujo tema era Industria 4.0. Participação das apresentações
                                            e desenvolvimento de projeto com base no tema.
                                        </p>
                                        <a href={require("../../assets/docs/bannerwec.pdf")} download="banner">
                                        <Button className="btl" inverted color='blue'>
                                            Mais detalhes 
                                        </Button>
                                        </a>
                                    </Item.Description>
                                </Item.Content>
                                <Item.Image className="img" size='medium' src={require('../../assets/imgs/proj-wec.jpg')} />
                            </Item>
                            <Item>
                                <Item.Image className="img" size='medium' src={require('../../assets/imgs/git.png')} />
                                <Item.Content>
                                    <p className="header-l">Outros projetos menores</p>
                                    <Item.Description>
                                        <p className="cont-">
                                            Para outros projetos menores confira meu GitHub. 
                                        </p>
                                        <a className="link" target="blank" href="https://github.com/Caiquesgoulart">
                                        <Button className="bt" inverted color='green'>
                                          Confira meu GitHub
                                        </Button>
                                        </a>
                                    </Item.Description>
                                </Item.Content>
                            </Item>
                        </Item.Group>
                    </ScrollAnimation>
                </p>
            </div>
        </div>
    )
}
export default Atvextra