import React from 'react'
import { Progress } from 'semantic-ui-react'
import ScrollAnimation from 'react-animate-on-scroll';
import './skills.css'

const Skills = () => {
    return (
        <div className="content-skills" id="content-skills">
            <div className="skills">
                <p>
                    <ScrollAnimation animateIn='animate__fadeInRight'>
                        <p className="h2sk">Basic skills...</p>
                        <div>
                            <h3 className="topcs">HTML5</h3>
                            <Progress percent={70} color='pink' inverted active progress />
                            <h3 className="topcs">CSS3</h3>
                            <Progress percent={40} color='blue' inverted active progress />
                            <h3 className="topcs">Javascript</h3>
                            <Progress percent={15} color='yellow' inverted active progress />
                        </div>
                    </ScrollAnimation>
                </p>
            </div>
        </div>
    )
}
export default Skills