import React from 'react'
import { Progress } from 'semantic-ui-react'
import ScrollAnimation from 'react-animate-on-scroll';
import { Button, Icon, Popup } from 'semantic-ui-react'
import { CopyToClipboard } from 'react-copy-to-clipboard'
import './contat.css'
const Contato = () => {
    return (
        <div className="content-contat" id="contat">
            <div className="contat">
                <p>
                    <ScrollAnimation animateIn='animate__fadeInRight'>
                        <p className="h2contat">Get in touch...</p>
                        <p className="topcscontat">Meus contatos:</p>
                        <p className="topcs">Abaixo meus links das redes.</p>   
                        <a href="https://www.linkedin.com/in/caiquesg" className="btcontat">
                            <Button color='linkedin'>
                                <Icon name='linkedin' /> LinkedIn
                            </Button>
                        </a>
                        <a href="https://github.com/Caiquesgoulart" className="btcontat">
                            <Button color='black'>
                                <Icon name='github' /> GitHub
                            </Button>
                        </a>
                        <a className="btcontat">
                        <CopyToClipboard text={'caique.sgcod@gmail.com'}>
                            <Popup
                                className='popup'
                                content='E-mail copiado para a área de transferência'
                                on='click'
                                pinned
                                trigger={
                                <Button className='btcontat' color='google plus' icon='mail' content='Email'/>
                            }                         
                            />
                            </CopyToClipboard>
                        </a>
                    </ScrollAnimation>
                </p>
            </div>
        </div>
    )
}
export default Contato