import React from 'react'
import { Progress } from 'semantic-ui-react'
import ScrollAnimation from 'react-animate-on-scroll';
import { Button } from 'semantic-ui-react'
import './cv.css'

const Cv = () => {
    return (
        <div className="content-cv" id="content-cv">
            <div className="cv">
                <p>
                    <ScrollAnimation animateIn='animate__fadeInRight'>
                        <p className="h2cv">Mais sobre mim...</p>                    
                            <p className="topcscv">Meu currículo:</p>   
                            <p className="topcs">Para saber mais sobre mim, minhas experiências e 
                            conhecimentos, confira meu currículo completo.</p>                
                        <a href={require("../../assets/docs/cv_Caique.pdf")} download="cv">
                            <Button className="btcv" inverted color='pink'>
                                Download do CV
                            </Button>
                        </a>
                    </ScrollAnimation>
                </p>
            </div>
        </div>
    )
}
export default Cv