import React from 'react'
import Typing from 'react-typing-animation';
import './home.css'

const Home = () => {
    return (
        <>
            <div className="home" id="home">
            </div>
            <div className="home-text">
                <Typing>
                    <span>
                        Olá! Eu me chamo Caique... Seja bem-vindo!
                    </span>
                </Typing>
            </div>
        </>
    )
}

export default Home 