import React from 'react'
import { Card } from 'semantic-ui-react'
import ScrollAnimation from 'react-animate-on-scroll';
import { Image, Reveal } from 'semantic-ui-react'
import { Grid } from 'semantic-ui-react'
import './quali.css'

const Quali = () => {
    return (
        <div className="content-quali">
            <div className="quali">
                <p>
                    <p className="h2ql">What i'm studying...</p>
                    <ScrollAnimation animateIn='animate__fadeInRight'>
                        <Card.Group itemsPerRow="5">
                            <Card className="cardb">
                                <Image src={require('../../assets/imgs/js.png')} wrapped ui={false} />
                                <div className="overlayjs"></div>
                                <Card.Content className="card">
                                    <Card.Header className="cardhjs">Javascript...</Card.Header>
                                    <Card.Description>
                                    </Card.Description>
                                </Card.Content>
                            </Card>
                            <Card>
                                <Image src={require('../../assets/imgs/react.png')} wrapped ui={false} />
                                <div className="overlayreact"></div>
                                <Card.Content>
                                    <Card.Header className="cardhreact">React...</Card.Header>
                                    <Card.Description>

                                    </Card.Description>
                                </Card.Content>
                            </Card>
                            <Card>
                                <Image src={require('../../assets/imgs/python.jpg')} wrapped ui={false} />
                                <div className="overlaypython"></div>
                                <Card.Content>
                                    <Card.Header className="cardhpython">Python...</Card.Header>
                                    <Card.Description>

                                    </Card.Description>
                                </Card.Content>
                            </Card>
                            <Card>
                                <Image src={require('../../assets/imgs/nodejs.png')} wrapped ui={false} />
                                <div className="overlaynode"></div>
                                <Card.Content>
                                    <Card.Header className="cardhnode">Node.js...</Card.Header>
                                    <Card.Description>

                                    </Card.Description>
                                </Card.Content>
                            </Card>
                            <Card>
                                <Image src={require('../../assets/imgs/coming2.png')} wrapped ui={false} />
                                <div className="overlaycoming"></div>
                                <Card.Content>
                                    <Card.Header className="coming">Coming...</Card.Header>
                                    <Card.Description>
                                    </Card.Description>
                                </Card.Content>
                            </Card>
                        </Card.Group>
                    </ScrollAnimation>
                </p>
            </div>
        </div >
    )
}
export default Quali